<template>
  <div>
    <table border style="width: 100%">
      <thead class="managerHeader">
        <th colspan="5"><i class="el-icon-user-solid"></i>赛事组成员</th>
      </thead>
      <tbody>
        <tr>
          <td
            align="center"
            width="10%"
            style="background-color: rgb(156, 204, 255)"
          >
            <i class="el-icon-s-tools"></i>研发部
          </td>
          <td class="bgColor">
            <little-table :data="technology"></little-table>
          </td>
          <td align="center">
            <el-tag>无敌</el-tag>
            <el-tag type="info">敲个不停</el-tag>
            <el-tag type="danger">熬夜猝死</el-tag>
          </td>
        </tr>
        <tr>
          <td
            align="center"
            width="10%"
            style="background-color: rgb(205, 253, 253)"
          >
            <i class="el-icon-document-copy"></i>管理部
          </td>
          <td width="70%" td class="bgColor">
            <little-table :data="manager"></little-table>
          </td>
          <td width="auto">
            <el-tag>谁敢惹我</el-tag>
            <el-tag type="info">禁言</el-tag>
            <el-tag type="danger">赛事迷</el-tag>
          </td>
        </tr>
        <tr>
          <td
            align="center"
            width="10%"
            style="background-color: rgb(106, 108, 206)"
          >
            <i class="el-icon-data-line"></i>技术部
          </td>
          <td td class="bgColor">
            <little-table :data="instructor"></little-table>
          </td>
          <td align="center">
            <el-tag>2：0</el-tag>
            <el-tag type="info">快点下班</el-tag>
            <el-tag type="danger">不要加班</el-tag>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import { getUserRoles } from "@/api/schedule/index";
import LittleTable from "./LittleTable.vue";
export default {
  name: "management",
  components: {
    LittleTable,
  },
  data() {
    return {
      technology: [],
      manager: [],
      instructor: [],
    };
  },
  methods: {
    initGetTechnology() {
      let params = {
        opname: "FrontEnd",
      };
      let parmas2 = {
        opname: "BackEnd",
      };
      getUserRoles(params)
        .then((res) => {
          let { data } = res;
          data.forEach((item) => {
            item.officium = "前端开发工程师";
          });
          this.technology = this.technology.concat(data);
        })
        .catch((err) => {
          console.log(err);
        });
      getUserRoles(parmas2)
        .then((res) => {
          let { data } = res;
          data.forEach((item) => {
            item.officium = "后端开发工程师";
          });
          this.technology = this.technology.concat(data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    initManage() {
      let params = {
        opname: "BOSS",
      };
      let parmas2 = {
        opname: "Arranger",
      };
      let parmas3 = {
        opname: "Secretary",
      };
      let parmas4 = {
        opname: "Recruiter",
      };
      getUserRoles(params)
        .then((res) => {
          let { data } = res;
          data.forEach((item) => {
            item.officium = "主办方";
          });
          this.manager = this.manager.concat(data);
        })
        .catch((err) => {
          console.log(err);
        });
      getUserRoles(parmas2)
        .then((res) => {
          let { data } = res;
          data.forEach((item) => {
            item.officium = "赛程排班";
          });
          this.manager = this.manager.concat(data);
        })
        .catch((err) => {
          console.log(err);
        });
      getUserRoles(parmas3)
        .then((res) => {
          let { data } = res;
          data.forEach((item) => {
            item.officium = "秘书";
          });
          this.manager = this.manager.concat(data);
        })
        .catch((err) => {
          console.log(err);
        });
      getUserRoles(parmas4)
        .then((res) => {
          let { data } = res;
          data.forEach((item) => {
            item.officium = "招募管理人";
          });
          this.manager = this.manager.concat(data);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    initGetAnchor() {
      let params = {
        opname: "Anchor",
      };
      getUserRoles(params)
        .then((res) => {
          let { data } = res;
          data.forEach((item) => {
            item.officium = "导播";
          });
          this.instructor = data;
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  mounted() {
    this.initGetTechnology();
    this.initManage();
    this.initGetAnchor();
  },
};
</script>

<style scoped lang="less">
table {
  border-collapse: collapse;
  .managerHeader {
    height: 50px;
    background-color: rgb(69, 113, 198);
    color: #fff;
  }
}
.el-tag {
  margin: 10px;
}
</style>
